import Color from "color"

//console.log(Color(primary).lighten(0.5).hex())

// THEME VARIABLES
const defaultFontFamily = `-apple-system, BlinkMacSystemFont, Arial, sans-serif`
// Color system
//
// Main colors
const primary = `#007aff`
const secondary = `#00ff62`
const white = `#fff`
//const gray = `#8e8e93`
const black = `#000`
//const transparent = `transparent`

// const ___ = `___`

export default {
  useColorSchemeMediaQuery: true,
  colors: {
    text: black,
    background: white,
    primary: primary,
    secondary: secondary,

    modes: {
      dark: {
        text: white,
        background: black,
        primary: Color(primary).lighten(0.5).hex(),
        secondary: secondary,
      },
    },
  },

  fonts: {
    body: defaultFontFamily,
  },
}
